import { Char } from "./char";
import { Token, TokenAttributes, TokenType } from "./token";

type CreateToken = (
  type: TokenType,
  value: string,
  pos: number,
  attributes?: TokenAttributes
) => void;

enum State {
  Initial,
  // Symbol,
  Vowel,
  Consonant,
  ConjunctConsonant,
}

const tokenize = (input: string): Token[] => {
  const tokens: Token[] = [];

  let acc = "";
  let varnasLength: number = 0;
  let state = State.Initial;

  const resetVariables = () => {
    acc = "";
    varnasLength = 0;
    state = State.Initial;
  };

  const createToken: CreateToken = (tokenType, value, pos, attributes) => {
    tokens.push(new Token(tokenType, value, pos, attributes));
    resetVariables();
  };

  for (let pos = 0; pos < input.length; pos += 1) {
    const char = new Char(input[pos]);

    const nextChar = new Char(input[pos + 1]);

    acc += char.value;

    switch (state) {
      case State.Initial: {
        if (char.isSymbol()) {
          createToken(TokenType.Symbol, acc, pos);
          break;
        }

        if (char.isWhitespace()) {
          createToken(TokenType.Whitespace, acc, pos);
          break;
        }

        if (char.isUnrecognised()) {
          createToken(TokenType.Unrecognised, acc, pos);
          break;
        }

        if (char.isVowel()) {
          if (nextChar.isVowelAttachment()) {
            state = State.Vowel;
            break;
          }

          createToken(TokenType.Akshara, acc, pos, { varnasLength: 1 });
          break;
        }

        if (char.isConsonant()) {
          if (nextChar.isNukta() || nextChar.isConsonantAttachment()) {
            varnasLength += 1;
            state = State.Consonant;
            break;
          }

          createToken(TokenType.Akshara, acc, pos, { varnasLength: 2 });
          break;
        }

        createToken(TokenType.Invalid, acc, pos);
        break;
      }

      case State.Vowel: {
        if (char.isAccent()) {
          createToken(TokenType.Akshara, acc, pos, { varnasLength: 1 });
          break;
        }

        if (char.isYogavaha()) {
          if (nextChar.isAccent()) {
            break;
          }

          createToken(TokenType.Akshara, acc, pos, { varnasLength: 1 });
          break;
        }

        break;
      }

      case State.Consonant: {
        if (char.isNukta()) {
          if (nextChar.isConsonantAttachment()) {
            break;
          }

          createToken(TokenType.Akshara, acc, pos, { varnasLength: 2 });
          break;
        }

        if (char.isVirama()) {
          if (nextChar.isJoiner()) {
            break;
          }

          if (nextChar.isConsonant()) {
            state = State.ConjunctConsonant;
            break;
          }

          createToken(TokenType.Akshara, acc, pos, { varnasLength });
          break;
        }

        if (char.isJoiner()) {
          if (nextChar.isJoiner()) {
            break;
          }

          if (nextChar.isConsonant()) {
            state = State.ConjunctConsonant;
            break;
          }

          createToken(TokenType.Akshara, acc, pos, { varnasLength });
          break;
        }

        if (char.isVowelMarkAttachment()) {
          varnasLength += 1;

          if (nextChar.isAccent()) {
            break;
          }

          createToken(TokenType.Akshara, acc, pos, { varnasLength });
          break;
        }

        if (char.isVowelMark()) {
          if (nextChar.isVowelMarkAttachment()) {
            break;
          }

          varnasLength += 1;
          createToken(TokenType.Akshara, acc, pos, { varnasLength });
          break;
        }
      }

      case State.ConjunctConsonant: {
        if (nextChar.isNukta() || nextChar.isConsonantAttachment()) {
          varnasLength += 1;
          state = State.Consonant;
          break;
        }

        varnasLength += 2;
        createToken(TokenType.Akshara, acc, pos, { varnasLength });
        break;
      }

      default: {
        break;
      }
    }
  }

  return tokens;
};

export default { tokenize };
